import React, { useEffect, useState } from 'react'
import './covid.css';

const Covid = () => {
    const [data, setData] = useState([]);


    const getCovidData = async () => {

        try {
            const res = await fetch('https://data.covid19india.org/data.json')
            const actualData = await res.json();
            console.log(actualData.statewise[0]);
            setData(actualData.statewise[0]);
        } catch (err) {
            console.log(err);
        }
    }

    useEffect(() => {
        getCovidData();
    }, []);


    return (
        <>
            <h1 className="live" > ðŸ”´Live</h1>
            <h2 className="covid" >COVID-19 CORONAVIRUS TRACKER</h2>
            <hr />
            <ul>
                <li className="cardIndia" >
                    <div className="card__main">
                        <div className="card__inner">
                       
                            <p className="card__name"><span>OUR</span> COUNTRY</p>

                            <p className="card__title" >INDIA</p>
                        </div>
                    </div>
                </li>

                <li className="cardGreen" >
                    <div className="card__main">
                        <div className="card__inner">
                        <ion-icon name="medkit" ></ion-icon>
                            <p className="card__name"><span>TOTAL</span> RECOVERED</p>
                            <p className="card__title" >{data.recovered}</p>
                        </div>
                    </div>
                </li>

                <li className="cardConfirmed" >
                    <div className="card__main">
                        <div className="card__inner">
                        <ion-icon name="checkmark"></ion-icon>
                            <p className="card__name"><span>TOTAL</span> CONFIRMED</p>
                            <p className="card__title" >{data.confirmed}</p>
                        </div>
                    </div>
                </li>

                <li className="cardDeath" >
                    <div className="card__main">
                        <div className="card__inner">
                        <ion-icon name="skull"></ion-icon>
                            <p className="card__name"><span>TOTAL</span> DEATHS</p>
                            <p className="card__title" >{data.deaths}</p>
                        </div>
                    </div>
                </li>

                <li className="cardActive" >
                    <div className="card__main">
                        <div className="card__inner">
                        <ion-icon name="notifications"></ion-icon>
                            <p className="card__name"><span>TOTAL</span> ACTIVE</p>
                            <p className="card__title" >{data.active}</p>
                        </div>
                    </div>
                </li>

                <li className="cardUpdated" >
                    <div className="card__main">
                        <div className="card__inner">
                        <ion-icon name="arrow-up-circle"></ion-icon>
                            <p className="card__name"><span>LAST</span> UPDATED</p>
                            <p className="card__title" >{data.lastupdatedtime}</p>
                        </div>
                    </div>
                </li>

            </ul>

        </>
    )
}

export default Covid
